@media only screen and (min-width:768px) {

.App {
  text-align: center;
  align-items: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #000000;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: stretch;
  font-size: calc(10px + 2vmin);
  color: rgb(0, 0, 0);
}

.App-link {
  color: #6800df;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@-webkit-keyframes AnimationName {
    0%{background-position:0% 50%}
    50%{background-position:100% 50%}
    100%{background-position:0% 50%}
}
@-moz-keyframes AnimationName {
    0%{background-position:0% 50%}
    50%{background-position:100% 50%}
    100%{background-position:0% 50%}
}
@keyframes AnimationName {
    0%{background-position:0% 50%}
    50%{background-position:100% 50%}
    100%{background-position:0% 50%}
}

.nftapp {
  background: radial-gradient(circle at 50% 50%, #000000, #000000, #000000, #000000, #000000);
}

@-webkit-keyframes AnimationName {
  0%{background-position:0% 50%}
  50%{background-position:100% 50%}
  100%{background-position:0% 50%}
}
@-moz-keyframes AnimationName {
  0%{background-position:0% 50%}
  50%{background-position:100% 50%}
  100%{background-position:0% 50%}
}
@keyframes AnimationName {
  0%{background-position:0% 50%}
  50%{background-position:100% 50%}
  100%{background-position:0% 50%}
}
.connectbutton {
  background: #ffffff;
  box-shadow: 0px 1px 10px #000000;
  color: #8826ff;
  border-radius: 14px;
  padding: 5px 12px;
  font-size: large;
  font-weight: 600;
  justify-items: stretch;
  align-items: center
}

.navbarfont {
  font-family: "SF Pro Display";
  font-weight: 300;
  justify-items: stretch;
  align-items: center
}

.nftminter {
    background: radial-gradient(circle at 50% 50%, #000000, #000000, #000000, #000000, #000000);
    font-family: "SF Pro Display";
    font-weight: 300;
    color: white;
    box-shadow: 0 0 1px 1px #5c006e;
    -webkit-border-radius: 20px !important;
    -moz-border-radius: 20px !important;
    border-radius: 20px !important;
    -webkit-border: 20px !important;
    -moz-border: 20px !important;
    border: 20px !important;
    justify-items: stretch;
    align-items: stretch
}

.nftstaker {
  background: radial-gradient(circle at 50% 50%, #000000, #000000, #000000, #000000, #000000);
  font-family: "SF Pro Display";
  font-weight: 300;
  color: white;
  display: flex;
  justify-content: center;
  box-shadow: 0 0 1px 1px #5c006e;
  -webkit-border-radius: 20px !important;
  -moz-border-radius: 20px !important;
  border-radius: 20px !important;
  -webkit-border: 20px !important;
  -moz-border: 20px !important;
  border: 20px !important;
  justify-items: stretch;
  align-items: stretch
}

.stakingrewards {
    font-family: "SF Pro Display";
    font-weight: 300;
    box-shadow: 1px 1px 15px #ffffff;
    background: radial-gradient(circle at 50% 50%, #000000, #000000, #000000, #000000, #000000, );
    justify-items: stretch;
    align-items: stretch
}

.modal-style1 {
  background: radial-gradient(circle at 50% 50%, #000000, #000000, #000000, #000000, #000000);
  font-family: "SF Pro Display";
  font-weight: 300;
  color: white;
  box-shadow: 0 0 1px 1px #5c006e;
  -webkit-border-radius: 20px !important;
  -moz-border-radius: 20px !important;
  border-radius: 20px !important;
  -webkit-border: 20px !important;
  -moz-border: 20px !important;
  border: 20px !important;
  justify-items: stretch;
  align-items: stretch
}

.container-style {
  justify-items: stretch;
  align-items: stretch
}

@keyframes glow2 {
  from {
    box-shadow: 0 0 4px -1px #e176f4;
  }
  to {
    box-shadow: 0 0 10px 7px #cc00ff;
  }
}

.stakegoldeffect {
    font-family: "SF Pro Display";
    font-weight: 300;
    animation: glow2 3s infinite alternate;
    background: linear-gradient(
      to bottom,
      rgb(9, 49, 110),
      rgba(255, 0, 0, 0)
    )
}

.errorsearch {
  background: black;
}

.nft-card {
  background-image: linear-gradient(125.83deg, rgb(0, 0, 0) 0%, rgb(121, 0, 221) 80.09%);
  background-size: 400% 400%;
  font-family: "SF Pro Display";
  color: white;
  box-shadow: 1px 1px 15px #ffffff;
  justify-items: stretch;
  align-items: stretch
}

.navbarglow {
  box-shadow: 0px 8px 15px #bb45ff;
}
.farm {
  background-image: radial-gradient( circle farthest-corner at 10% 20%,  rgb(0, 0, 0) 0%, rgba(32,45,58,1) 81.3% );
  font-family: "SF Pro Display";
  font-weight: 300;
  color: rgb(255, 255, 255);
  box-shadow: 0 0 1px 1px #ffffff;
    -webkit-border-radius: 20px !important;
    -moz-border-radius: 20px !important;
    border-radius: 20px !important;
    -webkit-border: 20px !important;
    -moz-border: 20px !important;
    border: 20px !important;
}
.farmcard {
color: rgb(255, 255, 255);
font-family: "SF Pro Display";
font-weight: 300;
background-image: linear-gradient( 112.1deg,  rgb(0, 0, 0) 11.4%, rgb(0, 0, 0) 70.2% );
}
.n2dtitlestyle {
  color: #ffffff;
  font-family: "SF Pro Display";
  font-weight: 400;
  justify-items: stretch;
  align-items: stretch
}

.nftportal {
  background: #000000;
  justify-items: stretch;
  align-items: stretch
}

.stakedcard {
  background: linear-gradient(270deg, #515151, #000000);
  background-size: 400% 400%;
    -webkit-animation: AnimationStake 14s ease infinite;
    -moz-animation: AnimationStake 14s ease infinite;
    animation: AnimationStake 14s ease infinite;
    font-family: "SF Pro Display";
    color: rgb(255, 255, 255);
    box-shadow: 1px 1px 11px #7615ff;
    justify-items: stretch;
    align-items: stretch
  }

@-webkit-keyframes AnimationStake {
    0%{background-position:0% 50%}
    50%{background-position:100% 50%}
    100%{background-position:0% 50%}
}
@-moz-keyframes AnimationStake {
    0%{background-position:0% 50%}
    50%{background-position:100% 50%}
    100%{background-position:0% 50%}
}
@keyframes AnimationStake {
    0%{background-position:0% 50%}
    50%{background-position:100% 50%}
    100%{background-position:0% 50%}
}
}

